  color: #191a19;
  cursor: pointer;
}

#loader {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  background: rgba(0, 0, 0, 0.75) url("../Photos/Loading_img.gif") no-repeat
    center center;
  z-index: 10000;
}

.Profile {
  border-radius: 50%;
  height: 100px;
  width: 100px;
}
